* {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  font-family: 'Roboto', -apple-system, system-ui,  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}


.todo {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 750px;
  min-height: 530px;
  transform: translate(-50%,-50%);
  box-shadow: 1px 2px 20px #F5E2F4;
  border-radius: 10px;
  border: 1px solid #F5E2F4;
  font-size: 14px;

  

  &__sidebar {
    background-color: #F7E4F5;
    width: 200px;
    min-height: 420px;
    border-right: 1px solid #f1f1f1;
    padding: 60px 20px;
  }
  &__tasks {
    flex: 1;
    padding: 60px;
  }
  
  }

  

  .field {

    box-sizing: border-box;

    background: #FFFFFF;
    border: 1px solid #EFEFEF;
    border-radius: 4px;
    padding: 8px 12px;
    width: 100%;

    &:focus {
      border-color: #dbdbdb;
    }
  }

  .button {

    background: #4DD599;
    border-radius: 4px;
    color: #fff;
    border: 0;
    padding: 10px 20px;
    cursor: pointer;
    &:hover {
      background: darken($color: #4DD599, $amount: 6%);
    }

    &:active {
      background: darken($color: #4DD599, $amount: 10%);
    }
  }


