.tasks {

   
    
    &__title {

    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;

    color: #64C4ED;
    padding-bottom: 20px;
    border-bottom: 1px solid #e4e2e2;

    &:hover {
        img {
            opacity: 0.2;
        }
    }

    img {
        opacity: 0;
        cursor: pointer;
        margin-left: 12px;
        margin-bottom: 3px;

        &:hover {
            opacity: 0.8;
        }
    }

    }

    &__items {
        margin-top: 30px;
        &-row {
            display: flex;
            align-items: center;
            margin-bottom: 15px;
      
           input {
            margin-left: 15px;
            border: 0;
            font-size: 16px;
            width: 100%;
           }
        }
    }

    
    .checkbox {

        &:hover {
            label {
                background-color: #F2F2F2;
                border-color: #F2F2F2;
                svg {
                    
                    path {
                        stroke:#b2b2b2;
                    }
                }
            }
        }
        
        input {
            display: none;
        }
        svg {
            transition: opacity 0.15s ease-in-out;
        }
        label {
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 2px solid #E8E8E8;
            border-radius: 30px;
            width: 20px;
            height: 20px;
            transition: opacity 0.15s ease-in-out;
            
            
        }

        input:checked + label {
            background-color: #4DD599;
            border-color: #4DD599;

            svg {
                path {
                    stroke: #ffff;
                }
            }
        }
    }
  }