.add-list {

    &__popup {
    
        width: 235px;
        height: 148px;
        background: #FFFFFF;
        box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.05);
        border-radius: 10px;
        padding: 18px;
        box-sizing: border-box;
        position: relative;
        top: -25px;
        left: 10px;

        &-close-btn {
            position: absolute;
            right: -8px;
            top: -8px;
            cursor: pointer;
        }

        &-colors {
            display: flex;
            justify-content: space-between;
            margin-top: 12px;
            
      
            .badge {
              width: 16px;
              height: 16px;
              cursor: pointer;
              border: 2px solid transparent;
              margin-right: 5px;
              &.active {
                border: 2px solid #525252;
              }
            }
          }
        .button {
            width: 100%;
            margin-top: 12px;
        }
    
    
    }

    
}